.footer {
	padding: 55px 0;
	background-color: rgb(22, 22, 22);
	color: #fff;
	&__container {
	}

	&__wrapper {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}

	&__left {
		font-size: 14px;
		line-height: 24px;
	}

	&__right {
		display: flex;
		flex-flow: column;
		align-items: flex-end;
		& > span {
			display: block;
			padding-bottom: 30px;
			font-size: 12px;
		}
	}

	&__policy {
		font-size: 12px;
		color: rgb(151, 151, 151);
		margin-bottom: 15px;
		a {
			color: rgb(151, 151, 151);
			&:hover {
				color: #fff;
			}
		}
	}

	&__button {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		height: 54px;
		width: 280px;
		font-size: 16px;
		color: #fff;
		border-radius: 15px;
		background-color: #3c99d3;
		font-weight: 700;
		transition: 0.2s;
		&:hover {
			box-shadow: 0 12px 30px rgba(60, 153, 211, 0.41);
		}
	}

	@media (--lg-phone) {
		padding: 32px 0 52px;

		&__wrapper {
			flex-flow: column;
			align-items: center;
		}

		&__button {
			height: 48px;
			font-size: 14px;
			margin: 0 auto;
			box-shadow: 0 12px 30px rgba(60, 153, 211, 0.41);
		}

		&__left {
			margin-bottom: 32px;
		}

		&__right {
			width: 100%;
			& > span {
				padding-bottom: 50px;
			}
		}
	}
}
