.capability {
	position: relative;
	margin-bottom: 50px;
	padding-bottom: 50px;

	&:after {
		transform: scaleY(-1);
		content: "";
		height: 72px;
		width: 100%;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		background-image: url("../images/divider.svg");
		background-position: 50% 50%;
		background-repeat: no-repeat;
	}

	&__container {}

	&__wrapper {
		margin: 0 -50px;
		display: flex;
		flex-flow: row wrap;
		position: relative;
		z-index: 2;
	}

	&__head {
		margin-bottom: 38px;
	}

	&__block {
		position: relative;
		max-width: calc((100% / 3) - 20px);
		margin: 0 10px;
		width: 100%;
		height: 212px;

		&:hover {
			.capability__content {
				box-shadow: 0 16px 50px rgba(0, 0, 0, 0.08);
				padding-bottom: 43px;
				z-index: 1;

				.capability__text {
					max-height: 500px;
				}
			}
		}
	}

	&__content {
		position: absolute;
		left: 0px;
		top: 0;
		border-radius: 20px;
		padding: 32px 40px 0px;
		transition: 0.5s;
		background-color: #fff;
		margin-bottom: 20px;
	}

	&__icon {
		margin-bottom: 20px;
	}

	&__title {
		font-size: 20px;
		line-height: 28px;
		color: #000;
		padding-bottom: 32px;
		max-width: 330px;
	}

	&__text {
		display: block;
		max-height: 0;
		overflow: hidden;
		font-size: 16px;
		line-height: 26px;
		transition: 0.5s;
	}

	@media (--lg-phone) {
		&:after {
			transform: none;
		}

		&__wrapper {
			margin: 0;
			flex-flow: column;
		}

		&__block {
			max-width: 100%;
			height: initial;
			margin: 0;

			&:hover {
				.capability__content {
					box-shadow: 0 8px 25px rgba(0, 0, 0, 0.06);
					padding-bottom: 18px;
					z-index: initial;
				}
			}
		}

		&__content {
			padding: 18px 24px;
			display: flex;
			flex-direction: row;
			position: relative;
			margin-bottom: 16px;
			box-shadow: 0 8px 25px rgba(0, 0, 0, 0.06);
			align-items: center;
		}

		&__text {
			display: none;
		}

		&__title {
			padding-bottom: 0;
			font-size: 16px;
			line-height: 24px;
			font-weight: 500;
		}

		&__icon {
			margin-bottom: 0;
			margin-right: 23px;
			max-width: 40px;
		}
	}
}