.partner {
	padding-bottom: 75px;
	margin-bottom: 65px;
	position: relative;
	&:after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		margin: 0 auto;
		width: 145px;
		height: 2px;
		background-image: url("../images/dotted.svg");
		background-repeat: no-repeat;
		background-position: 50% 50%;
	}
	&__container {
	}

	&__wrapper {
	}

	&__head {
		margin-bottom: 75px;
		font-size: 24px;
		font-weight: 700;
	}

	&__list {
		display: flex;
		flex-flow: row wrap;
	}

	&__item {
		width: calc(100% / 3);
		text-align: center;
	}

	&__image {
	}

	&__pic {
	}

	@media (--lg-phone) {
		padding-bottom: 50px;
		margin-bottom: 0;
		&:after {
			display: none;
		}
		&__head {
			font-size: 18px;
			text-align: center;
			margin-bottom: 45px;
		}

		&__list {
			flex-flow: column;
			align-items: center;
		}

		&__item {
			margin-bottom: 33px;
			width: 70%;
		}
	}
}
