.header {
	position: relative;
	z-index: 1;
	& + .page {
		margin-top: -170px;
	}
	&__container {
	}

	&__burger {
		display: none;
		width: 32px;
		height: 32px;
		margin-right: 24px;
		position: absolute;
		top: 30px;
		left: 20px;
		span {
			display: block;
			width: 24px;
			height: 2px;
			background-color: var(--blue);
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			&:after,
			&:before {
				position: absolute;
				content: "";
				width: 100%;
				background-color: inherit;
				height: 2px;
			}
			&:before {
				top: -7px;
			}
			&:after {
				top: 7px;
			}
		}
	}

	&__wrapper {
		display: flex;
		align-items: center;
		width: 100%;
		min-height: 205px;
	}

	&__logo {
		max-width: 315px;
		width: 100%;
		img {
			width: 100%;
		}
	}

	&__login {
		color: var(--blue);
		font-size: 16px;
		font-weight: 700;
		text-align: center;
		display: inline-flex;
		align-items: center;
		height: 46px;
		padding: 0 40px;
		border: 3px solid var(--blue);
		border-radius: 15px;
		transition: 0.2s;
		outline: none;
		&:hover {
			background-color: var(--blue);
			color: #fff;
			box-shadow: 0 12px 30px rgba(60, 153, 211, 0.41);
		}
	}

	@media (--lg-phone) {
		& + .page {
			margin-top: 0;
		}
		z-index: 99;
		margin-bottom: 32px;
		width: 100%;
		background-color: #fff;
		position: fixed;
		top: 0;
		left: 0;
		padding-bottom: 20px;
		&__burger {
			display: block;
		}
		&__logo {
			max-width: 25%;
			min-width: 110px;
			margin-right: 20px;
		}

		&__wrapper {
			min-height: initial;
			padding-top: 25px;
			padding-left: 56px;
			justify-content: space-between;
		}
	}
}
