.banner {
	margin-bottom: 65px;
	position: relative;

	&:after {
		content: "";
		height: 72px;
		width: 100%;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		background-image: url("../images/divider.svg");
		background-position: 50% 50%;
		background-repeat: no-repeat;
	}

	&_main {
		padding-bottom: 110px;
		padding-top: 170px;

		.banner__figure {
			position: absolute;
			top: 0;
			right: 0;
			width: 100%;
			height: 100%;

			&:after {
				content: "";
				width: 100%;
				height: 100%;
				position: absolute;
				background-image: url("../images/figure_1.png");
				background-position: calc(50% + 796px) -45px;
				background-repeat: no-repeat;
			}
		}

		.banner__left {
			width: 100%;
		}
	}

	&_second {
		margin-bottom: 64px;
		position: relative;

		&:after {
			position: absolute;
			z-index: 2;
			content: "";
			height: 137px;
			width: 100%;
			left: 0;
			bottom: 0;
			background-image: linear-gradient(to top, #fff, transparent);
		}

		.banner__figure {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			z-index: 0;
			display: flex;
			align-items: flex-end;
			justify-content: center;
			width: 100%;

			&-right {
				max-height: 205px;
				transform: translateX(90px);
			}

			&-left {
				transform: translateX(-270px);
			}
		}

		.banner__right {
			max-width: 390px;
			width: 100%;
			margin-left: 180px;
			flex: 1 0 auto;
		}

		.banner__left {
			flex: initial;
		}
	}

	&__container {
		position: relative;
		z-index: 1;
	}

	&__wrapper {
		display: flex;
		width: 100%;

		.banner_second & {
			max-width: 970px;
			margin: 0 auto;
		}
	}

	&__left {
		max-width: 900px;
		flex-shrink: 0;
	}

	&__block {
		margin-bottom: 70px;

		&>p {
			font-size: 28px;
			line-height: 36px;
			font-weight: 700;
			margin-bottom: 66px;

			.banner_second & {
				margin-bottom: 20px;
			}
		}

		&>span {
			display: block;
			margin-bottom: 66px;
		}

		&>a {
			font-size: 16px;
			color: var(--blue);
			font-weight: 700;
			line-height: 26px;

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__text {
		font-size: 18px;
		line-height: 26px;
		max-height: 130px;
		height: 100%;
		overflow-y: hidden;
		margin-bottom: 10px;
		max-width: 800px;

		&.active {
			max-height: 900px;
			transition: 0.5s;
		}
	}

	&__button {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		height: 54px;
		width: 280px;
		font-size: 16px;
		color: #fff;
		border-radius: 15px;
		background-color: var(--blue);
		font-weight: 700;
		transition: 0.2s;

		&:hover {
			box-shadow: 0 12px 30px rgba(60, 153, 211, 0.41);
		}
	}

	&__right {
		.banner__button {
			display: none;
		}
	}

	&__application {
		display: flex;
		margin: 0 -16px;

		&-link {
			margin: 0 16px;
			transition: 0.2s;

			&:hover {
				transform: translateY(-2px);
			}
		}
	}

	&__image {
		.banner_second & {
			max-width: 390px;
			max-height: 550px;
			overflow: hidden;
		}

		.banner_main & {
			margin-left: 100px;
		}
	}

	&__pic {}

	@media (--md-desktop) {
		&_main {
			padding-bottom: 60px;
			margin-bottom: 45px;

			.banner__right {
				transform: translateX(160px);
			}

			.banner__image {
				width: 618px;
				margin-left: 0;
			}
		}

		&_second {
			.banner__figure {
				&-left {
					transform: none;
				}
			}

			.banner__wrapper {
				max-width: initial;
				justify-content: space-between;
			}
		}

		&__left {
			max-width: 590px;
		}
	}

	@media (--lg-phone) {
		&_main {
			padding-top: 0;
			padding-bottom: 80px;
			margin-bottom: 60px;

			&:before {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				background-image: url("../images/figure_1.png");
				background-position: center;
				background-repeat: no-repeat;
				opacity: 0.4;
			}

			.banner__right {
				transform: none;
				padding: 0 27px;
			}

			.banner__image {
				margin: 0 auto;
				width: 100%;
				margin-bottom: 50px;
			}

			.banner__left {}

			.banner__figure {
				display: none;
			}
		}

		&_second {
			margin-bottom: 60px;

			&:after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				background-image: url("../images/figure_1.png");
				background-position: center;
				background-repeat: no-repeat;
				opacity: 0.4;
				z-index: 0;
			}

			&:before {
				content: "";
				height: 80px;
				width: 100%;
				position: absolute;
				bottom: 0px;
				z-index: 3;
				left: 0;
				right: 0;
				background-image: url(../images/divider.svg);
				background-position: 50% 50%;
				background-repeat: no-repeat;
			}

			.banner__right {
				padding: 0 80px;
			}

			.banner__image {
				margin: 0 auto;
				width: 100%;
				max-width: 200px;
				margin-bottom: 50px;
				margin-bottom: 0;

				&:after {
					content: "";
					height: 85px;
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					background-color: #fff;
				}

				img {
					margin-bottom: -45px;
				}
			}

			.banner__figure {
				display: none;
			}
		}

		&__wrapper {
			flex-flow: column;
		}

		&__block {
			&>p {
				font-size: 20px;
				line-height: 30px;
				margin-bottom: 35px;
			}
		}

		&__text {
			font-size: 14px;
			line-height: 26px;
		}

		&__right {
			text-align: center;
		}

		&__button {
			height: 48px;
			box-shadow: 0 12px 30px rgba(60, 153, 211, 0.41);

			.banner__left & {
				display: none;
			}

			.banner__right & {
				display: inline-flex;
				width: 100%;
			}
		}
	}
}