html {
	height: 100%;
}

body {
	font-size: 14px;
	font-family: "Geometria", sans-serif;
	padding: 0;
	margin: 0;
	background-color: #fff;
	overflow-x: hidden;

	&.fixed {
		// position: fixed;
		overflow: hidden;
		max-height: 100vh;
	}
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

button,
input {
	background-color: transparent;
	border: none;
}

ol,
ul,
li {
	padding: 0;
	margin: 0;
	list-style: none;
}

a {
	text-decoration: none;

	&:hover {
		text-decoration: none;
	}
}

img {
	max-width: 100%;
}

.container {
	max-width: 1680px;
	width: 100%;
	padding: 0 15px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
}

.page {
	&__container {
		overflow-x: hidden;
	}

	&__wrapper {}

	&__title {
		font-size: 32px;
		color: #000;
		line-height: 36px;
		font-weight: 700;
	}

	&__subtitle {
		font-size: 18px;
		margin-bottom: 48px;
		font-weight: 700;
	}
}

@media (--md-desktop) {
	.container {
		max-width: 1170px;
	}
}

@media (--lg-phone) {
	.page {
		padding-top: 103px;

		&__title {
			font-size: 22px;
		}

		&__subtitle {
			font-size: 16px;
		}
	}

	.container {
		max-width: 100%;
		padding: 0 20px;
	}
}

