.nav {
	&_header {
		flex: 1 0 0;
		display: flex;
		justify-content: flex-end;
		margin-right: 52px;
	}

	&__list {
		display: flex;
		align-items: center;
		margin: 0 -17px;
	}

	&__item {
		margin: 0 17px;
	}

	&__link {
		font-size: 16px;
		font-weight: 700;
		color: #000;
		&:hover {
			text-decoration: underline;
		}
	}

	@media (--lg-phone) {
		&_header {
			position: fixed;
			top: 90px;
			right: 0;
			width: 300px;
			margin-right: 0;
			transform: translateX(100%);
			transition: 0.2s;
			&.active {
				transform: translateX(-0%);
			}
		}
		&__list {
			flex-flow: column;
			background-color: var(--blue);
			width: 100%;
			padding: 20px 0;
		}

		&__link {
			color: #fff;
			margin-bottom: 20px;
			display: block;
			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	@media (--sm-phone) {
		&_header {
			width: calc(100% + 20px);
		}
	}
}
