.feedback {
	margin-bottom: 72px;

	&__container {}

	&__wrapper {}

	&__head {
		margin-bottom: 50px;
		font-size: 24px;
		font-weight: 700;
		text-align: center;
	}

	&__form {
		display: flex;
		flex-flow: column;
		align-items: flex-end;
		max-width: 1135px;
		width: 100%;
		margin: 0 auto;
		padding: 55px 55px;
		background-color: #fff;
		border-radius: 25px;
		box-shadow: 0 24px 100px rgba(0, 0, 0, 0.08);
	}

	&__field {
		width: 100%;
		margin-bottom: 16px;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	&__input {
		width: 100%;
		border: 2px solid rgb(218, 232, 238);
		border-radius: 11px;
		height: 40px;
		padding: 0 17px;
		font-size: 15px;
		outline: none;
		transition: 0.5s;
		font-family: "Geometria", sans-serif;

		&:focus {
			border-color: var(--blue);
		}
	}

	&__textarea {
		width: 100%;
		border: 2px solid rgb(218, 232, 238);
		border-radius: 11px;
		height: 173px;
		resize: none;
		padding: 13px 17px;
		font-size: 15px;
		outline: none;
		transition: 0.5s;
		font-family: "Geometria", sans-serif;

		&:focus {
			border-color: var(--blue);
		}
	}

	&__submit {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		height: 40px;
		width: 190px;
		font-size: 16px;
		color: #fff;
		border-radius: 11px;
		background-color: var(--blue);
		font-weight: 700;
		transition: 0.2s;
		margin-top: 24px;
		cursor: pointer;

		&:hover {
			box-shadow: 0 12px 30px rgba(60, 153, 211, 0.41);
		}
	}

	@media (--md-desktop) {
		&__form {
			max-width: 720px;
		}
	}

	@media (--lg-phone) {
		padding-top: 50px;
		background-color: rgb(250, 250, 252);

		&__head {
			font-size: 18px;
		}

		&__form {
			padding: 24px 24px 38px;
		}

		&__submit {
			margin: 24px auto 0;
			box-shadow: 0 12px 30px rgba(60, 153, 211, 0.41);
		}
	}
}