.about {
	padding-bottom: 90px;
	margin-bottom: 65px;
	position: relative;

	&_we {
		margin-bottom: 40px;
		padding-bottom: 80px;

		&:after {
			content: "";
			height: 72px;
			width: 100%;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			background-image: url("../images/divider.svg");
			background-position: 50% 50%;
			background-repeat: no-repeat;
		}
	}

	&_system {}

	&__container {}

	&__wrapper {}

	&__head {
		margin-bottom: 35px;
	}

	&__inner {
		display: flex;
		margin-bottom: 40px;
		flex-flow: row wrap;
	}

	&__left {
		max-width: 950px;
		width: 100%;
		flex: 1 0 0;
	}

	&__title {
		font-size: 24px;
		margin-bottom: 48px;
		font-weight: 700;
	}

	&__text {
		font-size: 18px;
		line-height: 26px;
	}

	&__right {
		text-align: center;
		margin-left: 100px;

		.about__title {
			display: none;
		}
	}

	&__image {}

	&__pic {}

	&__more {
		text-align: center;
		font-size: 16px;
		font-weight: 700;

		a {
			color: var(--blue);

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__button {
		margin-top: 40px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		height: 54px;
		width: 280px;
		font-size: 16px;
		color: #fff;
		border-radius: 15px;
		background-color: #3c99d3;
		font-weight: 700;
		transition: .2s;

		&:hover {
			box-shadow: 0 12px 30px rgba(60, 153, 211, .41);
		}
	}

	@media (--md-desktop) {
		margin-bottom: 30px;
		padding-bottom: 50px;

		&_we {
			.about__right {
				max-width: 480px;
				padding-top: 80px;
			}
		}
	}

	@media (--lg-phone) {
		&_we {
			padding-bottom: 70px;

			.about__right {
				padding-top: 0;
				max-width: initial;
			}

			&:after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				margin: 0 auto;
				width: 145px;
				height: 4px;
				background-image: url("../images/dotted.svg");
				background-repeat: no-repeat;
				background-position: 50% 50%;
			}
		}

		&_system {
			&:after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				margin: 0 auto;
				width: 145px;
				height: 2px;
				background-image: url("../images/dotted.svg");
				background-repeat: no-repeat;
				background-position: 50% 50%;
			}
		}

		&__right {
			margin: 0;
		}

		&__left {
			.about__title {
				display: none;
			}
		}

		&__right {
			margin-bottom: 75px;

			.about__title {
				display: block;
				text-align: left;
			}
		}

		&__head {
			margin-bottom: 40px;
		}

		&__title {
			font-size: 18px;
			margin-bottom: 28px;
		}

		&__inner {
			flex-flow: column-reverse wrap;
		}

		&__text {
			font-size: 14px;
			line-height: 26px;
		}

		&__more {
			text-align: left;
		}
	}
}