.modal {
	&__overlay {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		// background: rgba(0, 0, 0, 0.6);
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 999;
		background-image: url("../images/overlay.jpg");
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
	}

	&__container {
		background-color: #fff;
		padding: 30px;
		max-width: 500px;
		max-height: 100vh;
		border-radius: 25px;
		overflow-y: auto;
		box-sizing: border-box;

		.modal_login & {
			max-width: 450px;
			padding: 50px;
			width: 100%;
			text-align: center;
		}

		.modal_news & {
			max-width: 950px;
			width: 100%;
			padding: 10px 10px 50px;
			max-height: calc(100% - 40px);
		}

		.modal__exit {
			display: none;
		}
	}

	&__exit {
		position: absolute;
		right: 42px;
		top: 42px;
		width: 35px;
		height: 35px;
		background-image: url("../images/exit.svg");
		background-size: 32px;
		background-position: center;
		background-repeat: no-repeat;
		cursor: pointer;
	}

	&__tab {
		&-header {
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			border-radius: 11px;
			overflow: hidden;
			margin-bottom: 40px;
		}

		&-item {
			flex: 1 0 50%;
			text-align: center;
			height: 46px;
			line-height: 46px;
			background-color: #e5f0f4;
			font-size: 16px;
			font-weight: 700;
			color: var(--blue);
			transition: 0.2s;

			&[aria-disabled="false"] {
				background-color: var(--blue);
				color: #fff;
			}

			&:hover {
				background-color: #5db2e6;
				color: #fff;
			}
		}
	}

	&__content {}

	&__form {
		&-field {
			margin-bottom: 16px;
			width: 100%;
		}

		&-input {
			width: 100%;
			height: 40px;
			padding: 0 17px;
			font-size: 15px;
			color: #000;
			border-radius: 11px;
			border: 1px solid rgb(210, 210, 210);
			outline: none;
			transition: 0.2s;

			&:focus {
				border-color: #000;
			}
		}

		&-reset {
			color: var(--blue);
			font-size: 15px;
			font-weight: 700;

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__submit {
		margin-top: 8px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		height: 50px;
		width: 100%;
		font-size: 16px;
		color: #fff;
		border-radius: 15px;
		background-color: #3c99d3;
		font-weight: 700;
		transition: 0.2s;
		cursor: pointer;
		outline: none;
		margin-bottom: 12px;
	}

	@media (--lg-phone) {
		&__container {
			position: relative;

			.modal_login & {
				max-height: calc(100vh - 50px);
				padding: 50px 20px 20px;
				max-width: 340px;
			}

			.modal__exit {
				display: block;
				width: 30px;
				height: 30px;
				background-size: 15px;
				right: 20px;
				top: 10px;
				border-radius: 999px;
				background-color: var(--blue);
			}
		}

		&__exit {
			display: none;
		}

		&__submit {
			height: 40px;
		}
	}
}

@keyframes mmfadeIn {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}

@keyframes mmfadeOut {
	from {
		opacity: 1;
	}

	to {
		opacity: 0;
	}
}

@keyframes mmslideIn {
	from {
		transform: translateY(15%);
	}

	to {
		transform: translateY(0);
	}
}

@keyframes mmslideOut {
	from {
		transform: translateY(0);
	}

	to {
		transform: translateY(-10%);
	}
}

.micromodal-slide {
	display: none;
}

.micromodal-slide.is-open {
	display: block;
}

.micromodal-slide[aria-hidden="false"] .modal__overlay {
	animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="false"] .modal__container {
	animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__overlay {
	animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__container {
	animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
	will-change: transform;
}