.news {
	position: relative;
	padding-top: 20px;

	&__container {
		position: relative;
		margin-bottom: 65px;
	}

	&__figure {
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;

		&:after,
		&:before {
			content: "";
			width: 100%;
			height: 100%;
			background-repeat: no-repeat;
			background-position: center;
			position: absolute;
		}

		&:after {
			background-image: url("../images/figure_2.png");
			background-position: calc(50% + 690px) 0px;
		}

		&:before {
			background-image: url("../images/figure_3.png");
			background-position: calc(50% - 290px) -50px;
		}
	}

	&__head {
		margin-bottom: 50px;
		font-size: 24px;
		font-weight: 700;
	}

	&__wrapper {
		position: relative;
		z-index: 1;
		padding-bottom: 105px;

		&:after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			margin: 0 auto;
			width: 145px;
			height: 2px;
			background-image: url("../images/dotted.svg");
			background-repeat: no-repeat;
			background-position: 50% 50%;
		}
	}

	&__list {}

	&__item {
		max-width: 525px;
		// max-width: 300px;
		width: 100%;
		background-color: #fff;
		border-radius: 20px;
		overflow: hidden;
		padding: 0px 0px 40px;
		box-shadow: 0 16px 50px rgba(170, 169, 169, 0.08);
		// min-height: 695px;
		min-height: 595px;
		display: flex;
		flex-flow: column;
		align-items: flex-start;

		&.swiper-slide {
			// transform: scale(0.7) translate(585px, 0px);
			// transform: scale(0.7) translate(50%);
			transform: scale(0.6) translate(140%);
			transition: 0.5s;
			z-index: -1;

			&:after {
				position: absolute;
				left: 0;
				top: 0;
				content: "";
				width: 100%;
				height: 100%;
				background-color: rgba(255, 255, 255, 0.5);
				transition: 0.5s;
			}

			&-active {
				transform: scale(1) translate(0, 0);
				z-index: 3;

				&:after {
					background-color: rgba(255, 255, 255, 0);
					visibility: hidden;
				}
			}

			&-prev,
			&-next {
				z-index: 2;

				&:after {
					background-color: rgba(255, 255, 255, 0.75);
				}

				&_pre {
					z-index: 1;
				}
			}

			&-prev {
				// transform: scale(0.9) translate(150px, 0px);
				transform: scale(0.9) translate(10%, 0px);

				&_pre {
					transform: scale(0.7) translate(50%, 0px);
				}
			}

			&-next {
				// transform: scale(0.9) translate(-150px, 0px);
				transform: scale(0.9) translate(-10%, 0px);

				&_pre {
					transform: scale(0.7) translate(-50%, 0px);

					&~.swiper-slide {
						transform: scale(0.6) translate(-140%);
					}
				}
			}
		}
	}

	&__image {
		height: 220px;
		width: 100%;
		position: relative;
		overflow: hidden;
	}

	&__pic {
		object-fit: cover;
		height: 100%;
		min-width: 100%;
	}

	&__title {
		font-size: 20px;
		font-weight: 700;
		padding: 26px 32px 30px;
	}

	&__text {
		font-size: 16px;
		line-height: 26px;
		flex: 1 0 auto;
		padding: 0 32px;
		margin-bottom: 20px;
	}

	&__link {
		color: var(--blue);
		font-weight: 700;
		font-size: 16px;
		margin-left: 32px;
		display: inline-block;
		outline: none;

		&:hover {
			text-decoration: underline;
		}
	}

	&__arrows {

		&-prev,
		&-next {
			width: 32px;
			height: 24px;
			background-image: url("../images/arrow.svg");
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			outline: none;
			top: initial;
			bottom: 80px;

			&:after {
				display: none;
			}
		}

		&-prev {}

		&-next {
			transform: scaleX(-1);
		}
	}

	&__more {
		display: none;
		align-items: center;
		justify-content: center;
		height: 40px;
		width: 190px;
		font-size: 16px;
		color: #fff;
		border-radius: 12px;
		border: 2px solid var(--blue);
		color: var(--blue);
		font-weight: 700;
		transition: 0.2s;
		margin: 0 auto;
		line-height: 37px;
		text-align: center;
	}

	&__modal {
		&-image {
			height: 460px;
			padding-left: 65px;
			padding-bottom: 40px;
			padding-right: 20px;
			margin-bottom: 45px;
			position: relative;
			display: flex;
			align-items: flex-end;

			img {
				height: 100%;
				width: 100%;
				object-fit: cover;
				position: absolute;
				left: 0;
				top: 0;
				border-radius: 16px;
			}
		}

		&-title {
			position: relative;
			z-index: 1;
			color: #fff;
			font-size: 40px;
			font-weight: 700;
		}

		&-content {
			padding: 0 65px;
			margin-bottom: 70px;
		}

		&-header {
			display: flex;
			justify-content: space-between;
			margin-bottom: 30px;

			span {
				color: rgb(128, 128, 128);
				letter-spacing: 2px;
				font-size: 10px;
			}
		}

		&-text {
			font-size: 16px;
		}

		&-social {
			padding: 0 65px;

			ul {
				display: flex;
				justify-content: flex-end;

				li {
					width: 30px;
					height: 30px;
					margin-right: 15px;
					border-radius: 9999px;

					a {
						display: block;
						height: 100%;
						width: 100%;
					}

					&:last-of-type {
						margin-right: 0;
					}
				}
			}
		}
	}

	@media (--lg-phone) {
		margin-bottom: 60px;

		&__container {
			margin: 0 -20px;
		}

		&__wrapper {
			padding: 0 20px 60px;

			.swiper-wrapper {
				flex-flow: column;
			}
		}

		&__figure {
			display: none;
		}

		&__item {
			max-width: initial;
			min-height: initial;
			margin-bottom: 16px;
			box-shadow: 0 8px 25px rgba(0, 0, 0, 0.06);

			&.swiper-slide {
				transform: none;

				&:after {
					display: none;
				}
			}
		}

		&__image {
			height: initial;
			min-height: 240px;
		}

		&__pic {
			min-width: 100%;
		}

		&__title {
			padding: 15px 20px 10px;
			font-size: 16px;
		}

		&__text {
			padding: 0 20px 7px;
		}

		&__arrows {

			&-prev,
			&-next {
				display: none;
			}
		}

		&__more {
			display: block;
			margin-top: 30px;
		}

		&__head {
			padding: 0 20px;
			font-size: 18px;
			margin-bottom: 40px;
		}
	}
}